export default {
  name: 'Articles',
  async asyncData({ $axios, error }) {
    try {
      return await $axios.$get(`/articles`)
    } catch (e) {
      return error(e)
    }
  },
  head: {
    title: 'Artikel'
  },
  methods: {
    infiniteHandler($state) {
      this.$axios
        .$get(`/articles?page=${this.meta ? this.meta.current_page + 1 : 1}`)
        .then(({ data, meta, links }) => {
          this.data = [...this.data, ...data]
          this.meta = meta
          this.links = links
          $state.loaded()
          if(links.next === null) {
            $state.complete()
          }
        })
        .catch((e) => {
          $state.error()
        })
    },
  },
}
